import { css } from 'styled-components';
import { rems, settings } from './';

const hover = (...args) => css`
  &:hover,
  &:focus {
    html.non-touch & {
      ${css.call(null, ...args)};
    }
  }
`;

const aspectRatio = (srcWidth, srcHeight, maxWidth, maxHeight) => {
  let height = srcHeight;
  let width = srcWidth;

  if (maxWidth) {
    height = Math.ceil(maxWidth / srcWidth * srcHeight);
    width = maxWidth;
  } else if (maxHeight) {
    width = Math.ceil(maxHeight / srcHeight * srcWidth);
    height = maxHeight;
  }

  return { width, height };
};

const getSizeFromBreakpoint = (breakpointValue, max = false) => {
  const breakpoints = {
    desktop: 992,
    phone: 480,
    tablet: 768,
  };
  let mq = 1;
  if (breakpoints[breakpointValue]) {
    mq = max ? breakpoints[breakpointValue] - 1 : breakpoints[breakpointValue];
  } else if (parseInt(breakpointValue)) {
    mq = max ? breakpointValue - 1 : breakpointValue;
  } else {
    console.error('No valid breakpoint or size specified for media.');
  }
  return rems(mq);
};

const generateMedia = () => {
  const max = breakpoint => (...args) => css`
    @media (max-width: ${getSizeFromBreakpoint(breakpoint, true)}) {
      ${css.call(null, ...args)};
    }
  `;

  const min = breakpoint => (...args) => css`
    @media (min-width: ${getSizeFromBreakpoint(breakpoint)}) {
      ${css.call(null, ...args)};
    }
  `;

  const between = (firstBreakpoint, secondBreakpoint) => (...args) => css`
    @media (min-width: ${getSizeFromBreakpoint(
        firstBreakpoint,
      )}) and (max-width: ${getSizeFromBreakpoint(secondBreakpoint, true)}) {
      ${css.call(null, ...args)};
    }
  `;

  return {
    between,
    max,
    min,
  };
};

const media = generateMedia();
export { aspectRatio, hover, media };
