import React from 'react';
import i18next from './config';
import { I18nextProvider, withTranslation } from 'react-i18next';
import { settings } from '../ui';

const getWindowWidth = () => window.innerWidth;

export default (ns = 'translations') => WrappedComponent => {
  WrappedComponent = withTranslation(ns)(WrappedComponent);
  class withIntl extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        menuOpen: false,
        windowWidth: 0,
      };

      this.closeMenu = this.closeMenu.bind(this);
      this.toggleMenu = this.toggleMenu.bind(this);
      this.changeLanguage = this.changeLanguage.bind(this);
      this.windowOnResize = this.windowOnResize.bind(this);
    }

    changeLanguage(locale) {
      i18next.changeLanguage(locale);
    }

    closeMenu() {
      this.setState({ menuOpen: false });
    }

    toggleMenu() {
      this.setState({ menuOpen: !this.state.menuOpen });
    }

    windowOnResize() {
      const newWindowWidth = getWindowWidth();
      const shouldRemoveClasses = window.matchMedia(`(min-width: ${settings.breakpoints.tablet}px)`)
        .matches;

      if (this.windowWidth !== newWindowWidth && shouldRemoveClasses) {
        this.closeMenu();
      }
    }

    componentDidMount() {
      if (this.state.windowWidth !== getWindowWidth()) {
        this.setState({ windowWidth: getWindowWidth() });
        window.addEventListener('resize', this.windowOnResize);
      }

      const { pageContext } = this.props;

      if (pageContext) {
        const { locale } = pageContext;
        if (locale && locale !== i18next.language) {
          i18next.changeLanguage(locale);
        }
      }
    }

    componentWillUnmount() {
      window.removeEventListener('resize', this.windowOnResize);
    }

    render() {
      const providerValue = {
        menuOpen: this.state.menuOpen,
        closeMenu: this.closeMenu,
        toggleMenu: this.toggleMenu,
        changeLanguage: this.changeLanguage,
        ...this.props,
      };

      return (
        <I18nextProvider i18n={i18next}>
          <WrappedComponent {...providerValue} language={i18next.language} />
        </I18nextProvider>
      );
    }
  }

  return withIntl;
};
